import React, {Component} from 'react';
import Link from 'next/link';

export default class MenuList extends Component {
    state = {
        active: false
    }

    toggleActive = () => {
        this.setState({
            active: !this.state.active
        })
    }    

    render () {

        const linkStyle = {
            backgroundColor: '#1C541D', 
            color: 'white',
            textAlign: 'center',
            padding: '0.5rem'
        }

        const activeStyle = {
            backgroundColor: '#1C541D', 
            color: '#80CED7',
            textAlign: 'center'
        }        

        return (
            <li>
                <p onClick={this.toggleActive} style={linkStyle}><span><i style={{marginRight: '0.3rem', fontSize:'0.65rem'}} className={!this.state.active ? 'fa fa-plus' : 'fa fa-minus'}></i></span>{this.props.brand}</p>
                <ul style={{display: this.state.active ? 'block' : 'none'}}>                   
                    <Link href={`/${this.props.link}/cashback`}>
                        <li style={linkStyle} onClick={this.props.toggleClass}>
                            Cashback
                        </li>
                    </Link>

                    <Link href={`/${this.props.link}/cashback#application`}>
                        <li style={linkStyle} onClick={this.props.toggleClass}>
                            Application
                        </li>
                    </Link>

                    <Link href={`/${this.props.link}/review`}>
                        <li style={linkStyle} onClick={this.props.toggleClass}>
                            Review
                        </li>
                    </Link>                   
                </ul>
            </li>                  
        )
    }
}

